@font-face {
  font-family: 'icomoon';
  src:  url('/assets/fonts/icomoon.eot?dpou0f');
  src:  url('/assets/fonts/icomoon.eot?dpou0f#iefix') format('embedded-opentype'),
    url('/assets/fonts/icomoon.ttf?dpou0f') format('truetype'),
    url('/assets/fonts/icomoon.woff?dpou0f') format('woff'),
    url('/assets/fonts/icomoon.svg?dpou0f#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

